:host,
:root {
  --fa-font-solid: normal 900 1em/1 "Font Awesome 6 Pro";
}
@font-face {
  font-family: "Font Awesome 6 Pro";
  font-style: normal;
  font-weight: 900;
  font-display: block;
  src: url(./fa-solid-900.woff2) format("woff2"),
    url(./fa-solid-900.ttf) format("truetype");
}
